.App {
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	text-align: center;
}

ul {
	list-style: none;
}

.city-btn {
  cursor: pointer;
}

.EventList {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
}

.event {
	border: 1px solid #000;
	padding: 30px;
  margin: 10px;
}

input {
  padding: 10px;
  margin: 5px;
  border-radius: 5px;
}

button {
  border: none;
  padding: 10px;
  border-radius: 5px;
  cursor: pointer;
}

@media screen and (max-width: 500px) {
  .EventList {
    grid-template-columns: 1fr;
  }
}